@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box;
}


.product-card {
  border: 1px solid grey;
  border-radius: 10px;
  color: rgb(97, 97, 97);

  width: 290px;
  height: 430px;
  margin-bottom: 2rem;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-card figure {
  height: 70%;
}

.product-card img {
  max-width: 100%;
  max-height: 100%;
}

.product-name {
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.product-price {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.add-to-card-btn {
  background-color: rgb(238, 187, 45);
  padding: 0.8rem 0.4rem;
  border: 0;
  border-radius: 8px;
}

/* Animations */

.product-card img:active {
  -webkit-transform: scale(1.06);
          transform: scale(1.06);
}

.add-to-card-btn:active {
  background-color: rgb(216, 158, 66);
}

@media (min-width: 769px) {
  .product-card img:hover {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }

  .add-to-card-btn:hover {
    background-color: rgb(216, 158, 66);
  }
}

#products-page,
#products-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#products-page h2 {
  font-size: 3rem;
  color: rgb(100, 100, 100);

  margin-top: 1rem;
  margin-bottom: 3rem;
}

@media (min-width: 767px) {
  #products-list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
}

#navbar,
#navbar ul {
  display: flex;
}

#navbar {
  height: 10vh;
  background-color: rgb(70, 70, 85);

  justify-content: space-between;
  align-items: center;
}

h1 {
  color: white;
  font-size: 2.2rem;
  font-family: 'Monoton', cursive;
  margin-left: 1rem;
}

#navbar ul {
  width: 30%;

  justify-content: space-evenly;
}

#navbar ul {
  list-style-type: none;
}

#navbar a {
  -webkit-text-decoration: 0;
          text-decoration: 0;
  color: white;
}

#navbar img {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

@media (max-width: 500px) {
  #navbar {
    flex-direction: column;

    height: auto;
  }

  h1 {
    font-size: 3rem;
    margin-top: 1rem;
  }

  #navbar ul {
    width: 100%;

    margin: 1rem 0;
  }
}

#cart-header h2 {
  display: inline;
}

#cart-header,
#cart-outline,
#order-summary {
  width: 100%;
}

#cart-header {
  max-width: 550px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-top: 3rem;
  padding: 0 2rem;

  font-size: 2rem;
}

#cart-header > span:last-of-type {
  font-size: 1.5rem;
}

#cart-outline {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#order-summary {
  max-width: 70%;
}

#order-summary h2 {
  font-size: 2rem;
  margin-bottom: 3rem;
}

.cart-summary-item {
  display: flex;
  justify-content: space-between;

  margin-bottom: 1rem;
}

.cart-summary-item:last-of-type {
  font-weight: 600;
}

#checkout-btn {
  color: white;
  background-color: #6f6ff6;

  border: none;
  border-radius: 4px;
  margin-top: 2rem;
  padding: 0.6rem 1rem;
  text-transform: uppercase;
}

#checkout-btn:active {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

#no-cart-items {
  margin: 5rem 0;

  text-align: center;
}

@media (min-width: 769px) {
  #cart-page {
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 2rem;
    padding: 2rem;
  }

  #cart-outline {
    max-width: 1200px;
    display: grid;
    grid-template-rows: minmax(auto, 120px) auto;
    grid-template-columns: 3fr 2fr;

    background-color: rgb(226, 226, 226);
    border-radius: 15px;
    border: 1px solid #2e2e2e;
  }

  #item-list {
    max-height: 600px;
    overflow-y: scroll;
  }

  #cart-header {
    grid-row: 1/2;
    grid-column: 1/3;
  }

  #item-list {
    grid-row: 2/3;
    grid-column: 1/2;
  }

  #order-summary {
    grid-row: 2/3;
    grid-column: 2/3;

    margin-top: 0;
    padding-left: 3rem;
  }
}

@media (min-width: 1025px) {
  #cart-outline {
    padding-bottom: 2rem;
  }

  #no-cart-items {
    margin: 0;
  }
}

.cart-item {
  margin: 1.4rem;
  padding: 1rem;
  border-radius: 5px;

  max-height: 200px;
  max-width: 550px;

  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(5, 1fr);
}

.cart-item:not(:last-of-type) {
  border-bottom: 1px solid rgb(192, 192, 192);
}

.cart-item figure {
  grid-row: 1/3;
  grid-column: 1/3;
}

.cart-item img {
  height: 100%;
  width: 100%;
  object-fit: contain;

  -webkit-filter: drop-shadow(8px 8px 5px rgb(133, 130, 130));

          filter: drop-shadow(8px 8px 5px rgb(133, 130, 130));
}

.item-name {
  grid-row: 1/2;
  grid-column: 3/6;

  display: flex;
  align-items: center;

  font-size: 1.4rem;
}

.item-price {
  font-weight: 600;
}

.quantity-selector {
  grid-row: 2/3;
  grid-column: 4/6;

  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.remove-item-btn {
  grid-row: 3/4;
  grid-column: 4/6;

  max-width: 100px;
  margin: 0.5rem 10% 0.5rem 30%;

  text-transform: uppercase;
  background-color: white;
  border: 2px solid rgb(100, 100, 100);
}

.remove-item-btn:active {
  background-color: rgb(248, 130, 109);
}

@media (max-width: 374px) {
  .cart-item img {
    width: 80%;
    height: 80%;
  }

  .item-name {
    font-size: 1.2rem;
  }
  .remove-item-btn {
    font-size: 0.7rem;
    padding: 0;
  }
}

@media (min-width: 769px) {
  .remove-item-btn:hover {
    background-color: rgb(241, 150, 134);
  }
}

.dec-btn,
.inc-btn {
  border: none;
  border-radius: 50%;
}

.dec-btn,
.inc-btn {
  height: 25px;
  width: 25px;
}

.dec-btn:active {
  background-color: rgb(250, 148, 148);
}

.inc-btn:active {
  background-color: rgb(122, 251, 122);
}

.dec-btn {
  background-color: white;

  border: 1px solid rgb(179, 179, 179);
}

.quantity-selector input {
  max-width: 40px;

  margin: 0 0.5rem;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

@media (min-width: 769px) {
  .dec-btn:hover {
    background-color: rgb(250, 148, 148);
  }
  .inc-btn:hover {
    background-color: rgb(122, 251, 122);
  }

  .dec-btn:active,
  .inc-btn:active {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

#landing-page {
  height: 90vh;
  background: linear-gradient(90deg, #e9e9e9c2, #bebebe00),
    url(/shopping-cart/static/media/pexels-photo-10823474-removebg-preview.c555a331.png);
  background-position-x: right;
  background-size: contain;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

#tagline-div {
  width: 50%;
  color: rgb(83, 83, 83);

  margin-left: 3.5rem;
}

#tagline-div h2 {
  font-size: 4rem;

  margin-bottom: 2rem;
}
#tagline-div h3 {
  font-size: 2rem;
}

@media (max-width: 769px) {
  #tagline-div {
    width: 40%;
    color: rgb(41, 41, 41);
  }
}

@media (max-width: 500px) {
  #landing-page {
    background-position-x: left;
    background-size: cover;
  }

  #tagline-div {
    width: 60%;
    margin-left: 2rem;
  }

  #tagline-div h2 {
    font-size: 2.6rem;
  }
  #tagline-div h3 {
    font-size: 2rem;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

